import React, { useEffect, useRef, useState } from "react";

// import CSS
import "../../pages/Google-Education/google-education.css";
import "../Google-workspaces/workspaces.css";

import "../Google-workspaces/workspaces-responsive.css";
import "swiper/swiper-bundle.min.css";

// Images

import CUP from "../../assets/images/google-education/cup.png";
